@font-face {
    font-family: 'AdobeClean';
    src: local('AdobeClean'), url(../AdobeClean-Regular.ttf) format('truetype');
  }

.header {
    font-family: AdobeClean;
}

.hexagonRow {
    display: inline-block;
    width: 150px;
    height: 150px;
}


h2 {
    font-family: "Arial", sans-serif;
    text-align: center;
    font-size: 24px;
    margin-bottom: 30px;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: black;
  }

h3 {
    font-family: "Arial", sans-serif;
    text-align: center;
    font-size: 20px;
    padding-top: 5px;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: black;
  }

.hexagon {
    display: inline;
}

@media screen and (min-width: 768px) and (max-width: 995px) {
  h5 {
    font-size: small;
    margin-right: 100% !important;
  }

  svg.pinkDot {
    height: 30px;
  }

  div.row {
    margin-left: 10%;
  }
}

@media screen and (min-width: 376px) and (max-width: 767px) {
  h5 {
    font-size: small;
    margin-right: 100% !important;
  }

  svg.pinkDot {
    height: 24px;
  }

  h3 {
    font-size: 18px;
  }

  h6 {
    font-size: 14px;
  }

}

@media screen and (max-width: 375px) {
  h5 {
    font-size: small;
    margin-right: 100% !important;
  }

  svg.pinkDot {
    height: 24px;
  }

  h3 {
    font-size: 10px !important;
  }

  h6 {
    font-size: 12px !important;
  }

}


