@import 'node_modules/react-modal-video/scss/modal-video.scss';

.header {
    text-align: center;
    margin: auto;
}

body {
    height: 600px;
}

@media screen and (min-width: 768px) and (max-width: 995px) {
    .projects {
        margin: 0px !important;
    }

    div.col {
        margin-bottom: 10px;
    }

    .projectImage {
        height: 100% !important;
    }

    .ibm {
        width: 50px;
    }

    .projectButton {
        font-size: 10px !important;
    }

}

@media screen and (min-width: 376px) and (max-width: 767px) {
    .projects {
        margin: 0px !important;
    }

    div.col {
        margin-bottom: 10px;
    }

    .projectImage {
        width: 70% !important;
        height: 70% !important;
        margin-right: auto;
        margin-left: auto;
        margin-top: 10px;
    }

    .recent {
        margin-left: 50px !important;
        width: 50% !important;
        height: 50% !important;
    }

    .projectButton {
        font-size: 10px !important;
    }

    .card-title {
        font-size: 18px;
    }

    h6 {
        font-size: 13px;
    }

}

@media screen and (max-width: 375px) {
    .projects {
        margin: 0px !important;
    }

    div.col {
        margin-bottom: 10px;
    }

    .projectImage {
        width: 70% !important;
        height: 70% !important;
        margin-right: auto;
        margin-left: auto;
        margin-top: 10px;
    }

    .recent {
        margin-left: 25% !important;
        width: 50% !important;
        height: 50% !important;
        margin-left: auto !important;
        margin-right: auto !important;
    }

    .projectButton {
        font-size: 10px !important;
    }

    .card-title {
        font-size: 18px;
    }

    h6 {
        font-size: 13px;
    }

}