.header {
    text-align: center;
    margin: auto;
}

.card-horizontal {
    display: flex;
    flex: 1 1 auto;
}

.container {
    padding-bottom: 30px;
}

img {
    width: 100%;
}

@media screen and (min-width: 768px) and (max-width: 995px) {
    .internship {
        margin: 0px !important;
    }

    div.img-square-wrapper {
        margin-top: auto;
        margin-bottom: auto;
    }
}

@media screen and (max-width: 767px) {
    .internship {
        margin: 0px !important;
    }

    div.img-square-wrapper {
        margin-top: auto;
        margin-bottom: auto;
    }

    img {
        width: 140% !important;
    }

    li {
        font-size: 14px;
    }

    .card-title {
        font-size: 20px;
    }
}