@font-face {
    font-family: 'AdobeClean';
    src: local('AdobeClean'), url(../AdobeClean-Regular.ttf) format('truetype');
  }

#home {
  height: '100vh';
  min-height : '100vh';
}

.header {
    text-align: center;
    padding-top: 90px;
    margin: auto;
}

h1 {
  font-family: "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 4px;
  color: black;
}

h5 {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  text-align: center;
  padding: 4% 0;
  text-transform: uppercase;
  letter-spacing: 4px;
  color: black;
}

@media screen and (min-width: 768px) and (max-width: 995px) {
  .justify-content-md-center {
    margin: 0px !important;
  }

  .arrows {
    margin-top: 10vh !important;
  }

  .jump {
    margin: 0px !important;
  }
}

@media screen and (max-width: 767px) {
  .justify-content-md-center {
    margin-left: auto !important;
    margin-right: auto !important;
    justify-content: center;
  }

  .arrows {
    margin-top: 10vh !important;
  }

  .jump {
    margin: 0px !important;
  }
}

$size: 80px;
$speed: 3s;
$peakopacity:0.7;




body {
  .arrows {
    position: relative;
    /**/
    position: absolute;
    top:50%;
    left:50%;
    /**/
    width:$size;
    height:$size;
    transform:translate(-50%,-50%);
    &:before {
      content: '';
      position: absolute;
      width:100%;
      height:100%;
      border-left:$size/3 solid rgba(0,0,0,$peakopacity);
      border-bottom:$size/3 solid rgba(0,0,0,$peakopacity);
      transform:translate($size/3,$size*4/3)rotate(-45deg);
      animation: arrows $speed linear infinite;
    }
    &:after {
      content: '';
      position: absolute;
      width:100%;
      height:100%;
      border-left:$size/3 solid rgba(0,0,0,$peakopacity);
      border-bottom:$size/3 solid rgba(0,0,0,$peakopacity);
      transform:translate($size*2/3,0px)rotate(-45deg) ;
      animation: arrows $speed linear infinite $speed/-2;
    }
  }
}

@keyframes arrows {
  0% {
      border-left:$size/3 solid rgba(0,0,0,0);
      border-bottom:$size/3 solid rgba(0,0,0,0);
      transform:translate($size/-6,$size*4/-6)rotate(-45deg) ;
  }
  10%, 90% {
      border-left:$size/3 solid rgba(0,0,0,0);
      border-bottom:$size/3 solid rgba(0,0,0,0);
  }
  50% {
      border-left:$size/3 solid rgba(0,0,0,$peakopacity);
      border-bottom:$size/3 solid rgba(0,0,0,$peakopacity);
      transform:translate($size/-6,0px)rotate(-45deg) ;
  }
  100% {
      border-left:$size/3 solid rgba(0,0,0,0);
      border-bottom:$size/3 solid rgba(0,0,0,0);
      transform:translate($size/-6,$size*4/6)rotate(-45deg) ;
  }
}